"use client"

import { use, useEffect, useRef, useState } from "react";
import Link from "next/link";
import { supabase } from "@/utils/supabase/supabase";
import { useInfiniteScroll } from "@/hooks/useInfiniteScroll";

type Product = {
    id: string | number;
    name: string;
    image?: string | null;
    price?: number | null;
    description?: string | null;
};

export default function Products({ 
    initialProducts, 
    pageSize 
    }: { 
        initialProducts: Product[]; 
        pageSize: number;
    }) {

    const [adding, setAdding] = useState<Record<string | number, boolean>>({});

    function handleAdd(p: Product) {
        setAdding((s) => ({ ...s, [p.id]: true }));
        
        setTimeout(() => setAdding((s) => ({ ...s, [p.id]: false })), 700);
    }

    const [products, setProducts] = useState<Product[]>(initialProducts);
    const [loading, setLoading] = useState(false);
    const [hasMore, setHasMore] = useState(initialProducts.length === pageSize);
    const lastIdRef = useRef<number>(
        initialProducts.length > 0 ? Number(initialProducts[initialProducts.length - 1].id) : 0
    )
    const loaderRef = useRef<HTMLDivElement | null>(null);

    useEffect(() => {
        if(!loaderRef.current) return;

        const observer = new IntersectionObserver(
            (entire) => {
                const first = entire[0];

                if (first.isIntersecting && hasMore && !loading)  {
                    fetchNextPage();
                }
            },
            {
                root: null,
                rootMargin: "100px",
                threshold: 0,
            }
        );
        observer.observe(loaderRef.current);

        return () => observer.disconnect();
    },[loading, hasMore]);

    const fetchNextPage = async () => {
        if (loading || !hasMore) return;

        setLoading(true);

        const {data} = await supabase
            .from("products")
            .select("*")
            .gt("id", lastIdRef.current)
            .order("id", { ascending: true })
            .limit(pageSize)

        if (!data || data!.length === 0) {
            setHasMore(false);
            setLoading(false);
            return;
        } 
        lastIdRef.current = data[data.length - 1].id;

        setProducts((p) => {
            const map = new Map(p.map(item => [item.id, item]));
            data.forEach(item => map.set(item.id, item));
            return Array.from(map.values());
        });

        if (data.length < pageSize) setHasMore(false);

        setLoading(false);
}
        

    return (
        <section className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 mt-5">
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
                {products.map((p) => (
                    <article key={p.id} className="bg-white dark:bg-gray-800 rounded-lg overflow-hidden shadow hover:shadow-lg transition">
                        <Link href={`/product/${p.id}`} className="block">
                            <div className="w-full h-48 bg-gray-100 dark:bg-gray-700 overflow-hidden">
                                <img
                                    src={p.image ?? "https://imgholder.ru/600x300/8493a8/adb9ca&text=no+photo&font=kelson"}
                                    alt={p.name}
                                    className="w-full h-full object-cover"
                                />
                            </div>
                        </Link>

                        <div className="p-4">
                            <h3 className="text-sm font-medium text-gray-900 dark:text-gray-100 line-clamp-2">
                                {p.name}
                            </h3>
                            {p.description && <p className="mt-2 text-sm text-gray-600 dark:text-gray-300">{p.description}</p>}

                            <div className="mt-4 flex items-center justify-between">
                                <div className="text-lg font-semibold text-indigo-600 dark:text-indigo-400">
                                    {p.price != null ? `${p.price} ₽` : "—"}
                                </div>

                                <button
                                    onClick={() => handleAdd(p)}
                                    className="ml-4 inline-flex items-center px-3 py-1 bg-indigo-600 text-white text-sm rounded hover:bg-indigo-500 focus:outline-none focus:ring-2 focus:ring-indigo-300"
                                    aria-pressed={!!adding[p.id]}
                                >
                                    {adding[p.id] ? "Добавлено" : "В корзину"}
                                </button>
                            </div>
                        </div>
                    </article>
                ))}
            </div>
            

            {loading && <p className="text-center mt-4">Загрузка...</p>}
            {!hasMore && <p className="text-center mt-4">Больше продуктов нет</p>}

            {<p>{products.length}</p>}

            <div ref={loaderRef} className="h-10"></div>
        </section>
    );
}